
#buttons{
    display: flex;
    align-items: center;
    gap: 1rem;
    margin: 1rem 0;
    
    
}
#resume,.hire{
    background-color: var(--primary-color) !important;
    color: white;
    transition: background-color .2s ease-in-out, color .2s ease-in-out;
}

#resume:hover,.hire:hover{
    color: var(--primary-color);
    background-color: white !important;
};
