.myNavbar{
  display: flex;
  position: sticky; 
  top: 0;
  justify-content: space-between;
  align-items: center;
  padding: 0rem var(--pad1);
  left: 0;
  right: 0;
  background-color: var(--bg);
  z-index: 1024;
  border: 1px solid transparent;
}; 
.active {
  color: red !important;
}
.myNavbar li{
list-style: none;
padding: 1.3rem 20px;
}

.myNavbar ul{
display: flex;
}


.myNavbar a,.same{
text-decoration: none;
color:black;
cursor: pointer;
font-weight: 600;
font-size: var(--font1);
font-family: var(--fontf1);
}
.myNavbar a:hover,.same:hover{
  color: var(--primary-color);
  border-bottom: 2.8px solid var(--primary-color) !important;
}

.brand span{
transform: rotateY(0deg);
position: relative;
display: inline-block;
transition:transform .5s ease-in-out  ;
animation: flip 2s infinite;
animation-delay: calc(.5s * var(--i))
}
@keyframes flip {
0%,80% {
  transform: rotateY(360deg) 
}
}
.brand{
font-family: var(--fontf1);
font-size: 2.2rem;
color: var(--secondary-color);
position: relative
};
